import { Account } from "../../../../hooks/use_account";
import { SmartState } from "../../../../hooks/use_smart_state";
import { securePage } from "../../../../components/common/secure_page";
import Form from "../../../../components/common/form/form";
import Title from "../../../../components/common/form/title";
import { formParentManageAccount } from "../../manage-account";
import Box from "../../../../components/common/form/box";
import P from "../../../../components/common/form/p";
import Input from "../../../../components/common/form/input";
import SubmitButton from "../../../../components/common/form/submit-button";
import { formRequestAccountManagerAccountEmailConfirm } from "../../../../lib/shared/form-request-makeup/requests/account/manage-account/email/confirm";
import Desktop from "../../../../components/common/desktop";
import Mobile from "../../../../components/common/mobile";
import { useRouter } from "next/router";
import Spacer from "../../../../components/common/spacer";
import { misc } from "../../../../lib/misc";

export type AccountManageAccountEmailConfirmPageProps = {
    result: {};
    account: SmartState<Account | null>;
};

const AccountManageAccountEmailConfirmPage = securePage(function({result,account}: AccountManageAccountEmailConfirmPageProps) {
    const reactRouter = useRouter();

    const queryParams = misc.getQueryParams();
    const id = queryParams["id"];    

    return (<>
        <div className="account-manage-account-email-confirm-page">
            <Form 
                requestMakeup={formRequestAccountManagerAccountEmailConfirm}
                parent={formParentManageAccount}
                autoFocus
                onSuccess={() => {
                    return {
                        type: "redirect",
                        path: "/account/manage-account",
                        message: {
                            type: "info",
                            message: "The email associated with your account has been updated."
                        }
                    };
                }}
            >
                <Title>Change your email</Title>
                <Box border>
                    <P>
                        Please check your email and enter the OTP we sent you.
                    </P>                    
                    <Input name="otp" initialValue="" type="otp" />
                    <Input name="id" initialValue={id} type="hidden" />
                    <Spacer size={10} />
                    <SubmitButton caption="Confirm" disableIfOtpNotComplete="otp" />
                </Box>
            </Form>
        </div>
        <style jsx>{`
            .account-manage-account-email-confirm-page {
                /* min-height: 600rem; */
                width: 100%;
            }
        `}</style>
    </>
);
});

export default AccountManageAccountEmailConfirmPage;